import db from '@/firebase/init'
import Header from '@/components/layout/Header'

export default {
  name: 'Name',
  data(){
    return{
      textinput: null,
      feedback: null,
      isLoading: true
    }
  },
  components: {
    Header
  },
  methods: {
    greet(time){
      return `Hello there and good ${time}, ${this.textinput} `
    },
    doThis(e){
      // console.log(e.target.value)
      // this.$router.push({ name: 'About' })
      if(this.textinput){
        this.feedback = null

        db.collection('tests').add({
          textinput: this.textinput
        }).then(() => {
          this.$router.push({ name: 'About' })
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.feedback = "Je moet een naam invullen"
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.isLoading = false
    }, 3000)
  }
}
